@charset "utf-8";

.kolonsport-site {
  .kolon-header {
    .h-wrap {
      ul li .sub {
        a {
          font-size: 4.8vw;
        }
      }
    }
  }
  .wrap-form-login {
    .form-login {
      padding-bottom: 4vw;
      .btn-ctc {
        .contact-phone {
          font-size: 4vw;
        }
      }
      .in-txt {
        input {
          font-size: 4vw;
        }
      }
      .check-area {
        input + label + label {
          font-size: 4vw;
        }
      }
      .btns {
        .btn {
          font-size: 4vw;
        }
        .box-sub {
          .sub {
            font-size: 4vw;
          }
        }
      }
      .btn-login {
        background: #717171;
      }
      .btn-nonmbr {
        border: .266vw solid #cfcfcf;
      }
    }
    .under-line {
      width: 90.133vw;
      height: 2px;
      margin:0 auto;
      background: #eaeaea;
      
    }
  }
}

@media (min-width: 750px) {
  .kolonsport-site {
    .kolon-header {
      .h-wrap {
        ul li .sub {
          a {
            font-size: 36px;
          }
        }
      }
    }
    .wrap-form-login {
      .form-login {
        padding-bottom: 30px;
        .btn-ctc {
          .contact-phone {
            font-size: 30px;
          }
        }
        .check-area {
          input + label + label {
            font-size: 30px;
          }
        }
        .in-txt {
          input {
            font-size: 30px;
          }
        }
        .btns {
          .btn {
            font-size: 30px;
          }
          .box-sub {
            .sub {
              font-size: 30px;
            }
          }
        }
        .btn-nonmbr {
          border: 2px solid #cfcfcf;
        }
      }
    }
  }
}